.navbar{
    box-sizing: border-box;
    width: 100%;
    height: 8vh;
    background: #121619;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 150px;
}

.navbar-left{

    height: 100%;
    display: flex;
    justify-content: center;

    align-items: center;

}

.navbar-left img{
    height: 80%;
}

.navbar-right{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar a{
    color: #fff;
    text-decoration: none;
    margin: 20px;
    font-size: 20px;
}

.navbar-right button{
    background: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    display: none;
}
.navbar-right svg{
    font-size: 40px;
}

@media(max-width:1200px){
    .logo{
        display: none;

    }
    .navbar-right button{
        display: none;
    }
    .navbar{
        padding: 0 20px;

    }
}